<template>
    <div class="manage">
     
            <!-- 审核弹窗 -->
            <el-dialog title="请审核" :visible.sync="dialogAduitVisible" width="50%" :before-close="handleClose">
            <!-- 用户的表单信息 -->
            <el-form ref="form" :rules="rules" :inline="true" :model="form" label-width="80px">
                <el-table-column>
                <el-form-item label="审核状态" prop="status">
                    <el-radio v-model="form.status" label="审核通过"></el-radio>
                    <el-radio v-model="form.status" label="审核不通过"></el-radio>
                </el-form-item>
                </el-table-column>

                <el-table-column>
                <el-form-item label="审核意见" prop="reason">

                    <el-input placeholder="请输入审核意见" v-model="form.reason"></el-input>
                </el-form-item>
               </el-table-column>
                
            </el-form>
            <!-- 按钮区域 -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="cancel">取 消</el-button>
                <el-button type="primary" @click="submitAduit">确 定</el-button>
            </span>
        </el-dialog>
        
         <!-- 页头区域 -->
         <el-form :inline="true" :model="form" class="manage-header">
            <!-- 批量删除 -->
            <el-form-item>
                <el-button @click="handleBatchDelete" style="margin-left: 10px;" type="danger">
                    批量删除
                </el-button>
            </el-form-item>
            <!--form搜索区-->
            <el-form-item style="margin-left: 500px;">
                <el-input placeholder="请输入预约人姓名" v-model="deviceForm.userName" suffix-icon="el-icon-search"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button @click="load2" type="primary">查询</el-button>
                <el-button type="warning" @click="reset">重置</el-button>
            </el-form-item>
        </el-form>
    
      
        <!-- 主要表格 -->
        <el-table  :data="tableData" style="width: 100%" border stripe
            @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55" align="center"></el-table-column>
            <el-table-column type="index" label="序号" width="60" align="center" :index="indexMethod"></el-table-column>
            <el-table-column prop="userName" label="预约人姓名"  width="150" align="center"></el-table-column>
            <el-table-column prop="userId" label="预约人工号"  width="100" align="center"></el-table-column>
            <el-table-column prop="customerName" label="客户名称" align="center"></el-table-column>
            <el-table-column prop="orderPlace" label="预约场地"  width="150" align="center"></el-table-column>
            <el-table-column prop="startTime" label="开始时间"  width="160" align="center"></el-table-column>
            <el-table-column prop="endTime" label="结束时间"  width="160" align="center"></el-table-column>

    
            <el-table-column prop="submitTime" label="提交时间" type="datetime" width="170" align="center"></el-table-column>
            
            <el-table-column prop="status" label="审核状态" width="120" align="center">
                <template slot-scope="scope">
                    <el-tag type="success" v-if="scope.row.status === '审核通过'">审核通过</el-tag>
                    <el-tag type="warning" v-if="scope.row.status === '等待审核'">等待审核</el-tag>
                    <el-tag type="danger" v-if="scope.row.status === '审核不通过'">审核不通过</el-tag>
                </template>
            </el-table-column>
            <el-table-column prop="reason" label="审核意见" width="170" align="center" show-overflow-tooltip=""></el-table-column>

            <el-table-column label="操作" width="200" align="center">
                <template slot-scope="scope">
                    <el-button style="margin-left:5px" size="mini" type="success"  @click="handleAudit(scope.row)">审核
                    </el-button>
                    <el-button style="margin-left:5px" size="mini" type="danger" @click="handleDelete(scope.row.id)">删除
                    </el-button>
                </template>
            </el-table-column>
        </el-table>

        <!-- 页码 -->
        <div style="padding: 10px 0">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
                :page-sizes="[2, 5, 10, 20]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            dialogAduitVisible: false,
            tableData: [],
            modelType: 0,//0表示新增的弹窗，1表示编辑,
            multipleSelection: [],
            deviceForm:{},
            total: 0,//当前的总条数
            pageNum: 1,
            pageSize: 10,
            type: "third",
            form: {
                status:'',
                reason:'',
                // userName: '',
            },
            rules: {
        //验证用户名是否合法
            status: [
                        { required: true, message: "请选择审核状态", trigger: "blur" }
                    ],
            },
        }
    },
    created() {
        this.load()
    },
    methods: {
        submitAduit() {
            this.$refs.form.validate((valid) => {
                if (valid) {
                    //TODO:待继续完成审核
                    this.request.post("/equipmentOrder/update", this.form).then(res => {
                        if (res.code === "200") {
                            this.$message.success("保存成功")
                            this.dialogAduitVisible = false
                            this.load()
                        } else if(res.code === "402"){
                            this.$message.error(res.msg)
                        }
                        else {
                            this.$message.error("提交失败！")
                        }
                    })
                } else {
                    return false;
                }
            })
        },
        handleClose() {
            this.$refs.form.resetFields()
            this.dialogAduitVisible = false
            this.load()
        },
        cancel() {
            this.handleClose()
        },
        handleAudit(row){
            this.form= row
            this.dialogAduitVisible = true
            
        },
        // formatTimePeriod(timePeriod) {
        //     if (timePeriod === 1) {
        //         return '9:00-10:00';
        //     } else if (timePeriod === 2) {
        //         return '10:00-11:00';
        //     } else if (timePeriod === 3) {
        //         return '11:00-12:00';
        //     } else if (timePeriod === 4) {
        //         return '12:00-13:00';
        //     } else if (timePeriod === 5) {
        //         return '13:00-14:00';
        //     } else if (timePeriod === 6) {
        //         return '14:00-15:00';
        //     } else if (timePeriod === 7) {
        //         return '15:00-16:00';
        //     } else if (timePeriod === 8) {
        //         return '16:00-17:00';
        //     } else if (timePeriod === 9) {
        //         return '17:00-18:00';
        //     } else if (timePeriod === 10) {
        //         return '18:00-19:00';
        //     } else if (timePeriod === 11) {
        //         return '19:00-20:00';
        //     } else if (timePeriod === 12) {
        //         return '20:00-21:00';
        //     } else if (timePeriod === 13) {
        //         return '21:00-22:00';
        //     } else if (timePeriod === 14) {
        //         return '22:00-23:00';
        //     } else if (timePeriod === 15) {
        //         return '23:00-24:00';
        //     }
        // },
        indexMethod(index) {
            let curpage = this.pageNum   //单前页码，具体看组件取值
            let limitpage = this.pageSize   //每页条数，具体是组件取值
            return (index + 1) + (curpage - 1) * limitpage
        },
        handleDelete(id) {
            this.$confirm('此操作将永久删除该记录, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.request.delete("/equipmentOrder/back/" + id).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    }
                    // 重新获取列表的接口
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });
        },
        handleSelectionChange(val) {
            console.log(val)
            this.multipleSelection = val
        },
        handleBatchDelete() {
            if (this.multipleSelection.length === 0) {
                this.$message.warning("请先选择需要删除的内容");
                return;
            }

            let ids = this.multipleSelection.map(v => v.id)  //将对象数组[{},{}...]变成纯id的数组[1.2.3]
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.request.post("/equipmentOrder/del/batch", ids).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '批量删除成功!'
                        });
                    }
                    // 重新获取列表的接口
                    this.pageNum = 1
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消批量删除'

                });
                this.load()
            });
        },
        // changeEnable(row) {
        //     // console.log("1111")
        //     // this.request.post("/equipmentOrder", row).then(res => {
        //     this.request.post("/equipmentOrder/update", row).then(res => {

        //         if (res.code === '200') {
        //             this.$message.success("确认修改成功")
        //         } else if(res.code === '402'){  // 确认预约冲突设置
        //             this.$message.warning("已存在相同的预约，不可确认预约")
        //             row.enabled = false
        //         }
        //         else {
        //             this.$message.error("确认修改失败")
        //         }
        //     })
        // },
        load2(){
            this.pageNum = 1
            this.load()
        },
        load() {
            this.request.get("/equipmentOrder/page" , {
                params: {
                pageNum: this.pageNum,
                pageSize: this.pageSize,
                type: this.type,
                userName:this.deviceForm.userName
                }
            }).then(
                    res => {
                        console.log(res)
                        this.tableData = res.data.records
                        this.total = res.data.total
                    }
                )
        },
        handleCurrentChange(pageNum) {
            this.pageNum = pageNum
            this.load()
        },
        reset() {
            this.deviceForm.userName = ""
            this.load()
        },
        handleFileUploadSuccess() {
            this.$message.success("上传成功")
            this.load()
        },
        handleSizeChange(pageSize) {
            //   console.log(pageSize)
            this.pageSize = pageSize
            this.load()
        }
    },
};
</script>

<style lang="less" scoped>
.manage {
    height: 90%;

    .manage-header {
        display: flex;
        // justify-content: space-between;
        align-items: center;
    }

    .common-table {
        position: relative;
        height: calc(100% - 62px);

        .pager {
            // position: absolute;
            // bottom: 0;
            // right: 20px;
            padding: 10px 0;
        }
    }
}
</style>